.searchbar {
  display: flex;
  align-items: center;
}

.searchbar button {
  background: transparent;
  border: none;
  cursor: pointer;
}

.searchbar-input {
  border: none;
  border-radius: 20px;
  padding: 10px;
  width: 100%;
  height: 2.3rem;
  outline: none;
}

.icon-search {
  margin-left: 0.7rem;
  font-size: 2rem;
  color: white;
}

@media (min-width: 700px) {
  .searchbar {
    -webkit-animation-delay: 4s;
  }
}
